export default {
  /**
   * GOML
   * 
   * `<canvas>`
   * `<canvas>`(`width``height`)
   */
  goml: {
    components: ["CanvasInitializer", "LoopManager", "AssetLoadingManager", "GeometryRegistory", "RendererManager", "Fullscreen"],
  },
  /**
   * 3D
   * 
   * (`TransformComponent`)
   */
  scene: {
    components: ["Scene"],
  },
  /**
   * 3D
   * (`position``rotation`)
   * `TransformComponent`
   */
  object: {
    components: ["Transform"],
  },
  /**
   * 3D
   * 3D
   */
  camera: {
    components: ["Camera"],
    default: {
      position: [0, 0, 10],
    },
    super: "object",
  },
  /**
   * 3D **** 
   * 3D
   * ()()23D
   */
  mesh: {
    components: ["MaterialContainer", "MeshRenderer"],
    super: "object",
  },
  /**
   * 
   * gomlgoml
   * 1
   * `<render-XXX>`
   * `<renderer>``<render-scene>`
   */
  renderer: {
    components: ["Renderer"],
  },
  /**
   * 
   * (`scale``position``rotation`)
   */
  geometry: {
    components: ["Geometry"],
  },
  /**
   * 
   * url
   * 
   */
  texture: {
    components: ["Texture"],
  },
  /**
   * 
   * 
   * 
   */
  material: {
    components: ["Material"],
  },
  /**
   * 
   * Grimoire.js(*.sort)
   */
  "import-material": {
    components: ["MaterialImporter"],
  },
  /**
   * 
   * `<renderer>`
   * 
   * 
   */
  "texture-buffer": {
    components: ["TextureBuffer"],
  },
  /**
   * /`renderbuffer`
   * `<renderer>`
   * 
   */
  "render-buffer": {
    components: ["RenderBuffer"],
  },
  /**
   *
   * 
   * `<renderer>`
   * `out`()
   */
  "render-scene": {
    components: ["RenderScene", "RenderHitArea"],
    default: {
      material: null,
    },
  },
  /**
   * 
   * `<renderer>`
   * `out`()(`quad`)
   */
  "render-quad": {
    components: ["MaterialContainer", "RenderQuad"],
    default: {
      material: null,
    },
  },
};



